<template>
  <div>
    <h1>Bar Chart</h1>
    <svg :width="width" :height="height">
      <g ref="chart">
        <rect
          v-for="(d, i) in data"
          :key="i"
          :width="w"
          :height="calcHeight(d)"
          :x="xScale(d)"
          :y="yScale(d)"
          stroke="#ff6347"
          strokeWidth="3"
          fill="#f5f5f5"/>
      </g>
      <g ref="axis"></g>
    </svg>
  </div>
</template>

<script>
import { select } from 'd3-selection';
import { axisLeft, axisBottom } from 'd3-axis';
import { scaleBand, scaleLinear } from 'd3-scale';
import { data } from '../store';

const xSelector = d => d.x;
const ySelector = d => d.y;

const xScale = scaleBand().range([0, 400]).domain(data.map(d => xSelector(d))).padding(0.3);
const yScale = scaleLinear().range([420, 0]).domain([0, 500]);

export default {
  name: 'BarChart',
  data () {
    return {
      width: 500,
      height: 500,
      w: xScale.bandwidth(),
      data: data
    }
  },
  mounted () {
    const margin = { top: 40, left: 40, bottom: 40, right: 0 };
    const yAxis = axisLeft(yScale).tickSizeInner(-420);
    const xAxis = axisBottom(xScale);

    const chartWidth = this.width - (margin.left + margin.right);
    const chartHeight = this.height - (margin.top + margin.bottom);

    select(this.$refs.chart)
      .attr('width', chartWidth)
      .attr('height', chartHeight)
      .attr('transform', `translate(${margin.left}, ${margin.top})`);

    select(this.$refs.axis).append('g')
      .attr('transform', `translate(${margin.left}, ${margin.top})`)
      .attr('class', 'axis y')
      .call(yAxis);

    select(this.$refs.axis).append('g')
      .attr('transform', `translate(${margin.left}, ${chartHeight + margin.top})`)
      .attr('class', 'axis x')
      .call(xAxis);
  },
  methods: {
    yScale: function (d) {
      return yScale(ySelector(d));
    },
    xScale: function (d) {
      return xScale(xSelector(d));
    },
    calcHeight: function (d) {
    const margin = { top: 40, left: 100, bottom: 40, right: 0 };

    const chartHeight = this.height - (margin.top + margin.bottom);

      const yValue = yScale(ySelector(d));
      const barHeight = chartHeight - yValue;
      return barHeight;
    }
  }
}
</script>

<style>
.tick line {
  stroke-dasharray: 2 2;
  stroke: #ccc;
}
</style>
